.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.notvis{
    visibility: hidden;
}

div#wrapper{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(45deg, #e66465, #9198e5);
    background-image: url(./poker_stol.alice.2.PNG);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
div#cards{
    position: absolute;
    top: 2vmin;
    left: 5vmin;
}
div.card{
    display: inline;
    margin: 30px;
}
div.card > img{
    height: 180px;
    width: 130px;
}

div.dialer_cards{
    margin-top: 1vmin;
    margin-left: 50vmin;
}

div.table_cards{
    margin-top: 30px;
    margin-left: 20vmin;
}
div.user_cards{
    margin-top: 30px;
    margin-left: 50vmin;
}

div#messages{
    position: fixed;
    z-index: 999;
    bottom: 0;
    left: 0;
    width: 90%;
    height: 20vh;
    margin-left: 220px;
    color: white;
}

@media screen and (min-width: 0px) and (max-width: 560px){
    /* 559 x 568 */
    div#cards{
	position: absolute;
	top: 2vmin;
	left: 5vmin;
    }

    div.card{
	display: inline;
	margin: 10px;
    }

    div.card > img{
	height: 74px;
	width: 59px;
    }
    
    div.dialer_cards{
	margin-top: 10vmin;
	margin-left: 34vmin;
    }
    
    div.table_cards{
	margin-top: 30px;
	margin-left: 10vmin;
    }
    
    div.user_cards{
	margin-top: 30px;
	margin-left: 34vmin;
    }

    div#messages{
	position: fixed;
	bottom: 0;
	left: 0;
	width: 90%;
	height: 30vh;
	margin-left: 30px;
	color: white;
    }
}

@media screen and (min-width: 561px) and (max-width: 770px){
