	margin-top: 10vmin;
	margin-left: 10vmin;
    }
    
    div.table_cards{
	margin-top: 20px;
	margin-left: 10vmin;
    }
    
    div.user_cards{
	margin-top: 20px;
	margin-left: 10vmin;
    }

    div#messages{
	position: fixed;
	bottom: 10;
	right: 0;
	width: 60%;
	height: 30vh;
	margin-left: 280px;
	color: white;
