}

@media screen and (min-width: 771px) and (max-width: 960px){
    /* 959 x 400 */
    div#cards{
	position: absolute;
	top: 10px;
	left: 30vmin;
    }

    div.card{
	display: inline;
	margin: 10px;
    }

    div.card > img{
	height: 84px;
	width: 59px;
    }
    
    div.dialer_cards{
	margin-top: 10vmin;
	margin-left: 10vmin;
    }
    
    div.table_cards{
	margin-top: 20px;
	margin-left: 10vmin;
    }
    
    div.user_cards{
	margin-top: 20px;
	margin-left: 10vmin;
    }

    div#messages{
	position: fixed;
	bottom: 10;
	right: 0;
	width: 62vw;
	height: 30vh;
	margin-left: 320px;
	color: white;
    }
}

